/*
  Colour palette: Interpro
*/
body {
  & a.label,
  & .label,
  & a:hover,
  & a:focus,
  & a:active {
    color: #2199e8;
  }

  & h1 {
    color: #f7f7f7;
  }
  & h2 {
    color: rgb(0, 124, 130);
  }
  & h3,
  & h5 {
    color: #444;
  } /*is H3 defined as #222 in ebi-global*/

  & a.special {
    background-color: #7cd17c;
  }

  & .menu .active > a,
  & .tag,
  & .button,
  & .button.primary {
    background-color: rgb(0, 124, 130);
  }
  /*custom color for button*/
  & .button {
    background-color: #2daec1;
  }
  /*custom color for disabled buttons*/
  & .button.disabled,
  & .button[disabled] {
    background-color: #2daec1;
    &:hover {
      background-color: #2daec1;
      opacity: 0.25;
    }
  }
  & .warning {
    background-color: #2daec1;
  }
  & .button.primary:hover,
  .button.primary:focus,
  & .button:hover,
  & .button:focus,
  & .button:active {
    background-color: #2daec1;
    /*opacity: 1; SP not sure where this is needed*/
  }

  & div.local-masthead {
    background-color: rgb(0, 124, 130);
  }
  /*Overwrite EBI global - needed to be added to theme.css*/
  & input:checked ~ .switch-paddle {
    background: #2daec1;
  }
  & a.white-color,
  & a.white-color:visited {
    color: var(--colors-vlightgray, #d4d4d4);
  }
  & a.white-color:hover,
  & a.white-color:focus {
    color: var(--colors-almost-white, #f8f8f8);
    text-decoration: underline;
  }
}
/* end */
